<h1 id="angular" tabindex="-1"><a class="header-anchor" href="#angular" aria-hidden="true">#</a> Angular</h1>
<h2 id="description" tabindex="-1"><a class="header-anchor" href="#description" aria-hidden="true">#</a> Description</h2>
<p>C'est un framemork frontend Javascript très populaire et pratique pour concevoir
des <strong>SPA</strong> (Single Page Application).</p>
<h2 id="installation" tabindex="-1"><a class="header-anchor" href="#installation" aria-hidden="true">#</a> Installation</h2>
<p>Télécharger et installer NodeJS pour disposer de node et de npm</p>
<ol>
<li>Installer <strong>Angular CLI</strong></li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> <span class="token function">install</span> -g @angular/cli
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ol>
<li>Créer un nouveau projet</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>ng new nomProjet
<span class="token builtin class-name">cd</span> nomProjet
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><ol>
<li>Lancer le server intégré</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>ng serve
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ol>
<li>Lancer la page <a href="http://localhost:4200/" target="_blank" rel="noopener noreferrer">Lien<OutboundLink/></a></li>
</ol>
